<script>
import { filter, replace, map, pick } from 'lodash'
import axios from 'axios'
import Score from '@/components/Score.vue'
import Gallary from '@/components/Gallary.vue'
export default {
  name: 'app',
  components: { Score, Gallary },
  data() {
    return {
      bonus: {
        moneytree: '00.00',
        classic: '暂未开放',
        treasure: '暂未开放'
      },
      user: '0',
      userName: '试玩',
      balance: 0.0,
      token: '0',
      gameList: [],
      apiConfig: {
        list: 'http://47.88.170.136:3000',
        bonus: 'http://testgame.risheng3d.com:45557/getBonusConfig',
        stage: 'develop',
        realm: 'ali'
      },
      login: {
        userName: '',
        userPwd: '',
        buId: 0
      }
    }
  },
  created() {
    if (
      window.location.hostname == 'webpage.risheng3d.com' ||
      window.location.hostname == 'webpage.na77.org'
    ) {
      const search = window.location.search.substring(1)
      let params = { userId: '947117', token: '0' }
      map(search.split('&'), query => {
        const key = decodeURIComponent(query.split('=')[0])
        const val = decodeURIComponent(query.split('=')[1])
        params[key] = val
      })
      params = pick(params, ['userId', 'token'])
      this.user = params['userId']
      this.token = params['token']
      if (
        window.location.hostname == 'webpage.risheng3d.com' //'192.168.3.161'
      )
        this.apiConfig = {
          list: 'http://a1.na12345.com:3000/player/gameList/',
          bonus: 'http://game.risheng3d.com:45557/getBonusConfig',
          realm: 'ali',
          stage: 'prod',
          balanceApi: 'https://n1admin.na12345.com/player/balance/'
        }
      if (window.location.hostname == 'webpage.na77.org')
        this.apiConfig = {
          list: 'http://a1.na12345.com:3000/player/gameList/',
          bonus: 'http://game.risheng3d.com:45557/getBonusConfig',
          realm: 'aws',
          stage: 'prod',
          balanceApi: 'https://n1admin.na12345.com/player/balance/'
        }
    } else {
      this.apiConfig = {
        list: 'http://47.88.170.136:3000/gameList/70000?isAll=true&userId=',
        bonus: 'http://testgame.risheng3d.com:45557/getBonusConfig',
        realm: 'ali',
        stage: 'develop',
        balanceApi: 'https://d3rqtlfdd4m9wd.cloudfront.net/player/balance/'
      }
    }
    axios.get(this.apiConfig.list + this.user).then(res => {
      this.gameList = res.data.payload || res.data
    })

    setInterval(() => {
      if (this.token + '' !== '0') {
        axios
          .get(this.apiConfig.balanceApi + this.user, {
            headers: { Authorization: this.token }
          })
          .then(res => {
            if (res.data.code + '' == '0') {
              this.balance = parseFloat(res.data.payload).toFixed(2)
            }
          })
      }
      axios
        .post(this.apiConfig.bonus, {
          gameType: 'all'
        })
        .then(res => {
          this.bonus.moneytree = (
            (res.data.config[0] &&
              res.data.config[0]['gameType'] === 'moneytree' &&
              res.data.config[0]['bonusPool']) ||
            0.0
          ).toFixed(2)
        })
    }, 5000)
  },
  methods: {
    isAuth: function() {
      return this.user !== '0'
    },
    auth: function() {
      axios
        .post(
          'https://xbbw2jfb58.execute-api.ap-southeast-1.amazonaws.com/dev/game/login',
          this.login
        )
        .then(res => {
          this.user = res.data.data.userId
          this.userName = res.data.data.username
          this.balance = res.data.data.balance
          this.token = res.data.data.token
        })
    },
    getUser: function() {
      return this.user === '0' ? '游客' : '用户: ' + this.user
    },
    gameUrl: function(gameId) {
      const items = filter(this.gameList, game => {
        return game.kindId == gameId
      })
      return items[0] && items[0].gameLink
        ? replace(
            replace(items[0].gameLink, /:token/, this.token),
            /:userId/,
            this.user
          ) + '&from=develop'
        : 'javascript:void(0)'
    },
    gameData: function(gameId) {
      const items = filter(this.gameList, game => {
        return game.kindId == gameId
      })
      return items[0] && items[0].gameImgAli ? items[0].gameImgAli : '-1'
    }
  }
}
</script>

