<template>
  <div>
    <b-row class="brand-row">
      <b-col v-for="(item,index) in images.slice(0,3)" :key="index">
        <a
          target="_blank"
          class="img-holder"
          :href="item.gameUrl"
          :style="{backgroundImage:'url(' + item.imgUrl + ')'}"
        ></a>
      </b-col>
    </b-row>
    <b-row class="brand-row">
      <b-col v-for="(item,index) in images.slice(3,6)" :key="index+3">
        <a
          target="_blank"
          class="img-holder"
          :href="item.gameUrl"
          :style="{backgroundImage:'url(' + item.imgUrl + ')'}"
        ></a>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { map, filter, replace, sortBy } from 'lodash'
export default {
  name: 'Gallary',
  props: {
    gameIds: {
      type: Array,
      required: true,
      default: () => []
    },
    gameData: {
      type: Array,
      required: true,
      default: () => []
    },
    realm: {
      type: String,
      default: () => 'ali'
    },
    stage: {
      type: String,
      default: () => 'develop'
    },
    token: {
      type: String,
      default: () => '0'
    },
    userId: {
      type: [String, Number],
      default: () => '0'
    }
  },
  computed: {
    images: function() {
      return sortBy(
        map(this.gameIds, id => {
          const items = filter(this.gameData, game => game.kindId == id)
          if (
            items[0] &&
            items[0].gameImgAli &&
            items[0].gameImg &&
            items[0].gameLink &&
            items[0].sortOrder
          ) {
            const gameBaseUrl = replace(
              replace(items[0].gameLink, /:token/, this.token),
              /:userId/,
              this.userId
            )
            const imgUrl =
              this.realm === 'ali' ? items[0].gameImgAli : items[0].gameImg
            const gameUrl =
              this.stage === 'develop'
                ? gameBaseUrl + '&from=' + this.stage
                : gameBaseUrl
            const sortOrder = items[0].sortOrder
            return { imgUrl, gameUrl, sortOrder }
          } else {
            return { imgUrl: '-1', gameUrl: 'javascript:void(0)', sortOrder: 0 }
          }
        }),
        ['sortOrder']
      )
    }
  }
}
</script>


