<template>
  <div class="number-line-wrap">
    <p v-for=" digit in digits" :class="digit.val" :key="digit.id">
      <span></span>
    </p>
  </div>
</template>

<script>
import { map } from 'lodash'
export default {
  name: 'Score',
  props: {
    score: String
  },
  computed: {
    digits: function() {
      return map(this.score, (dig, index) => {
        return {
          val: dig !== '.' ? 'number-wrap n-' + dig : 'number-wrap n-dot',
          id: index
        }
      })
    }
  }
}
</script>

